<template>
  <div class="notice_box">
    <el-dropdown trigger="click" @command="onCallView">
      <el-badge class="el-dropdown-link" :is-dot="isNewMessage">
        <i class="el-icon-caret-bottom el-icon-bell" />
      </el-badge>
      <el-dropdown-menu v-if="true" slot="dropdown" class="dropdown_group">
        <el-dropdown-item v-for="item in userMessage" :key="item.id" class="clearfix" :command="item">
          <el-avatar :src="item.fromAccount.userImageUrl" :size="25" />
          <span class="name">{{ item.fromAccount.name }}</span>
          <el-badge class="mark" :value="item.unreadMsg.length === 0 ? '' : item.unreadMsg.length" />
        </el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
  name: 'NoticeCmp',

  data() {
    return {
    }
  },

  methods: {
    ...mapActions(['getHistoryMessages', 'clearUnreadCount']),

    // 点击未读消息列表
    onCallView(command) {
      this.clearUnreadCount({ mimc_appAccount: command.fromAccount.id, sequence: command.unreadMsg })
      this.$emit('childFnUser')
      this.getHistoryMessages(command.fromAccount)
    }
  },

  watch: {
  },

  computed: {
    ...mapGetters(['isNewMessage', 'userMessage', 'historyMessages'])
  }
}
</script>

<style lang="scss" scoped>
  .el-dropdown-menu{
    min-width: 120px;
  }
  .notice_box{
    line-height: 0px;
    display: inline-block;
    margin-right: 15px;
    .dropdown_group{
      max-height: 100px;
      overflow: auto
    }
  }
  .clearfix{
    display: flex;
    align-items: center;
    .name{
      margin-left: 10px;
    }
  }
</style>
