<template>
  <div class="mask" @click="onMask">
    <div class="rongyun_box" @click.stop>
      <div class="info">
        <el-avatar shape="square" :src="currentUser.userImageUrl" :size="30" />
        <span class="name">{{ currentUser.name }}</span>
      </div>
      <div id="scrolldIV" class="log">
        <div v-for="item in historyMessages" :key="item.id">
          <div v-if="currentUser.id === item.fromAccount" class="log_item">
            <el-avatar class="portrait" :src="currentUser.userImageUrl" :size="30" />
            <blockquote v-if="item.message.bizType === 'TEXT'" class="bubble">{{ item.message.payload }}</blockquote>
            <el-image v-else-if="item.message.bizType === 'PIC_FILE' && RegExp(/woho_/).test(item.message.payload)" class="img_biaoqing" fit="cover" :src="woho_img(item.message.payload)[0].imgUrl" />
            <el-image v-else-if="item.message.bizType === 'PIC_FILE'" class="el_image" fit="cover" :src="item.message.payload" />
          </div>
          <div v-else class="my_log_item">
            <blockquote v-if="item.message.bizType === 'TEXT'" class="bubble">{{ item.message.payload }}</blockquote>
            <el-image v-else-if="item.message.bizType === 'PIC_FILE' && RegExp(/woho_/).test(item.message.payload)" class="img_biaoqing" fit="cover" :src="woho_img(item.message.payload)[0].imgUrl" />
            <el-image v-else-if="item.message.bizType === 'PIC_FILE'" class="el_image" fit="cover" :src="item.message.payload" />
            <el-avatar class="portrait" :src="userImage" :size="30" />
          </div>
        </div>
      </div>
      <div class="icon_group">
        <transition name="el-zoom-in-bottom">
          <div v-show="biaoqingShow" class="biaoqing">
            <div class="biaoqing_group">
              <div v-for="(item, i) in biaoqingJson" :key="i" class="biaoqing_item" @click="onBiaoQing(item)">
                <el-image class="biaoqing_img" :src="item.imgUrl" :alt="item.imgName" fit="cover" />
              </div>
            </div>
          </div>
        </transition>
        <div class="icon_box" @click="biaoqingShow=!biaoqingShow"><i class="iconfont icon">&#xe64e;</i></div>
        <el-upload action="http-request" :http-request="onUpload" :show-file-list="false" multiple>
          <div class="icon_box"><i class="iconfont icon">&#xe8ba;</i></div>
        </el-upload>
      </div>
      <textarea v-model="msg" class="input_box" rows="5"></textarea>
      <div class="btn_group">
        <el-button size="mini" @click="onMask">关闭</el-button>
        <el-button size="mini" @click="onSend(msg, 'TEXT')">发送</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions, mapMutations } from 'vuex'
import { MIXINS_UPLOADFILE_EVNET } from '@/mixins/event.js'
import biaoqingJson from '@/assets/json/biaoqing.json'

export default {
  name: 'RongyunCmp',

  mixins: [
    MIXINS_UPLOADFILE_EVNET
  ],

  data () {
    return {
      imgUrl: require('@/assets/img/biaoqing/woho_10001.png'),
      msg: '', // 消息输入框内容
      userImage: localStorage.userImage,
      biaoqingJson: biaoqingJson, // 表情包数据
      biaoqingShow: false, // 表情框的显示与隐藏
    }
  },

  methods: {
    ...mapActions(['sendMsg']),
    ...mapMutations(['addMessage']),

    // 点击关闭
    onMask() {
      this.$emit('childFnMask') // 聊天窗口
    },
    // 点击发送
    onSend (msg, bizType) {
      if (msg === '') {
        return false
      }
      let data = {
        userName: this.currentUser.id,
        sendMessage: msg,
        bizType: bizType,
      }
      this.sendMsg(data)
      let msgJson = {
        fromAccount: localStorage.userId,
        message: {
          bizType: bizType,
          payload: msg
        }
      }
      this.addMessage(msgJson)
      this.msg = ''
    },
    onScroll () {
      let div = document.getElementById('scrolldIV')
      div.scrollTop = div.scrollHeight
    },

    // 点击发送图片
    async onUpload (params) {
      let imgUrl = await this.uploadFile(params)
      this.onSend(imgUrl, 'PIC_FILE')
    },

    // 点击发送表情
    onBiaoQing (item) {
      this.biaoqingShow = false
      this.onSend(item.imgId, 'PIC_FILE')
    },

    // 私有方法---------------------------------------------------------
    // 先把表情包图片加载了防止后期加载不出来
    getImage () {
      this.biaoqingJson = biaoqingJson.map(item => {
        item.imgUrl = require('@/assets/img/biaoqing/' + item.imgId + '.png')
        return item
      })
    },

    // 表情文字转换表情
    woho_img (msg) {
      return this.biaoqingJson.filter((item) => item.imgId === msg)
    }
  },

  created () {
    this.getImage()
  },

  computed: {
    ...mapGetters(['currentUser', 'historyMessages']),
  },

  watch: {
    historyMessages () {
      setTimeout(() => {
        this.onScroll()
      }, 500)
    }
  }
}
</script>

<style lang="scss" scoped>
  $themColo: #85c1e5;
  .mask{
    position: fixed;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 999999;
  }
  .rongyun_box{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 430px;
    height: 550px;
    border: $themColo 1px solid;
    background-color: $themColo;
    z-index: 9999999;
    .info{
      display: flex;
      align-items: flex-start;
      margin: 20px auto auto 10px;
      .name{
        margin-left: 5px;
      }
    }
    .log::-webkit-scrollbar { width: 0 !important }
    .log{
      width: 424px;
      height: 325px;
      background-color: rgb(238, 238, 238);
      border: 1px solid $themColo;
      margin: 27px auto auto auto;
      overflow: auto;
      .log_item{
        margin: 5px;
        display: flex;
        align-items: flex-start;
        .portrait{
          margin-top: 10px;
        }
        .bubble{
          word-break: break-all;
          display: inline-block;
          background-color: #fff;
          position: relative;
          padding: 10px;
          margin: 1em 0em 0em 1em;
          min-width: 10px;
          max-width: 300px;
          min-height: 16px;
          line-height: 1.2;
          text-align: center;
          font-size: 14px;
          -moz-box-shadow: 1px 1px 5px #888888; /* 老的 Firefox */
          box-shadow: 1px 1px 5px #888888;
          -moz-border-radius: 7px;
          -webkit-border-radius: 7px;
          border-radius: 7px;
        }
        .bubble:after {
          content: "\00a0";
          width: 0px;
          height: 0px;
          border-top: 8px solid #fff;
          border-left: 14px solid transparent;
          position: absolute;
          top: 13px;
          left: -10px;
        }
        .img_biaoqing{
          max-width: 50px;
          margin-left: 15px;
        }
        .el_image{
          max-width: 300px;
          margin-left: 5px;
        }
      }
      .my_log_item{
        margin: 5px;
        display: flex;
        justify-content: flex-end;
        align-items: flex-start;
        position: relative;
        .portrait{
          margin-top: 10px;
        }
        .bubble{
          word-break: break-all;
          display: inline-block;
          background-color: yellowgreen;
          padding: 10px;
          margin: 1em 1em 0em 1em;
          min-width: 10px;
          max-width: 300px;
          min-height: 16px;
          line-height: 1.2;
          text-align: center;
          font-size: 14px;
          -moz-box-shadow: -1px 1px 5px #888888; /* 老的 Firefox */
          box-shadow: -1px 1px 5px #888888;
          -moz-border-radius: 7px;
          -webkit-border-radius: 7px;
          border-radius: 7px;
        }
        .bubble:after {
          content: "\00a0";
          width: 0px;
          height: 0px;
          border-top: 8px solid yellowgreen;
          border-right: 14px solid transparent;
          position: absolute;
          top: 25px;
          left: 368px;
        }
        .img_biaoqing{
          max-width: 50px;
          margin-right: 15px;
        }
        .el_image{
          max-width: 300px;
          margin-right: 5px;
        }
      }
    }
    .icon_group{
      display: flex;
      position: relative;
      .biaoqing::-webkit-scrollbar { display: none }
      .biaoqing{
        word-break: break-all;
        display: inline-block;
        background-color: #fff;
        position: absolute;
        top: -210px;
        left: 5px;
        width: 270px;
        height: 170px;
        padding: 15px;
        overflow: auto;
        .biaoqing_group{
          display: flex;
          flex-wrap: wrap;
          .biaoqing_item{
            width: 33px;
            height: 33px;
            position: relative;
            .biaoqing_img{
              position: absolute;
              top: 50%;
              left: 50%;
              transform: translate(-50%, -50%);
              width: 25px;
            }
          }
          .biaoqing_item:hover{
            background-color: #e7e7e7;
          }
        }
      }
      .biaoqing:after {
        content: "\00a0";
        width: 0px;
        height: 0px;
        border-top: 10px solid #e3e3e3;
        border-left: 10px solid transparent;
        border-right: 10px solid transparent;
        position: absolute;
        top: 200px;
        left: 2px;
      }
      .icon_box{
        z-index: 1;
        cursor: pointer;
        .icon{
          font-size: 20px;
          margin-left: 7px;
        }
      }
      .icon_box:hover{
        color: #ff7a29;
      }
    }
    .input_box{
      width: 420px;
      height: 80px;
      display: block;
      resize: none;
      margin: 0px auto auto auto;
    }
    .btn_group{
      margin: 5px auto auto 300px;
    }
  }
</style>
