<template>
  <div class="app-wrapper">
    <transition name="el-fade-in">
      <chatWindowCmp v-if="rongyunCmp" @childFnMask="onChildFnMask" />
    </transition>
    <el-container class="home-container">
      <el-aside :width="isCollapse ? '58px' : '200px'">
        <!-- Aside content -->
        <el-menu
          mode="vertical"
          :default-active="$route.path"
          router
          background-color="#304156"
          text-color="#fff"
          active-text-color="#ffd04b"
          :unique-opened="true"
          :collapse="isCollapse"
          :collapse-transition="false"
          @select="handleSelect"
        >
          <el-submenu v-for="item in menulist" :key="item.id" class="el_submenu" :index="item.id">
            <template slot="title">
              <i :class="item.menuIcon" />
              <span class="ml-2"></span>{{ item.menuName }}
            </template>
            <el-menu-item
              v-for="subItem in item.childList"
              :key="subItem.id"
              :index="subItem.menuUrl"
            >
              {{ subItem.menuName }}
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>
      <el-container>
        <el-header style="text-align: right; font-size: 12px">
          <div class="toggle-button mt-2" @click="toggleCollapse">|||</div>
          <noticeCmp style="cursor: pointer" @childFnUser="onChildFnUser" />

          <el-dropdown v-if="false">
            <!-- 订单-->
            <el-button class="mr-1" style="color: #000" type="text">订单</el-button>
            <el-dropdown-menu slot="dropdown" class="order_group">
              <div v-if="false">
                <el-dropdown-item class="order_item" @click.native="$router.push({path: `/orderList/orderContent?id= + '723535120955491fa021461511aa77b3'`})">
                  <el-avatar size="medium" src="https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png"></el-avatar>
                  <div class="order_info">
                    <div class="order_header">
                      <span style="color: #000;">11111111222222222222222222223</span>
                      <span class="ml-1" style="color: #999999">08-09</span>
                    </div>
                    <span class="order_title">22222222222222222222</span>
                  </div>
                  <el-image class="order_img" :src="`https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=1985153060,170879988&fm=26&gp=0.jpg`" fil="scale-down"></el-image>
                </el-dropdown-item>
                <el-dropdown-item class="order_item" @click.native="add">
                  <el-avatar size="medium" src="https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png"></el-avatar>
                  <div class="order_info">
                    <div class="order_header">
                      <span style="color: #000;">11111111222</span>
                      <span class="ml-1" style="color: #999999">08-09</span>
                    </div>
                    <span class="order_title">22222222222222222222</span>
                  </div>
                  <el-image class="order_img" :src="`https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=1985153060,170879988&fm=26&gp=0.jpg`" fil="scale-down"></el-image>
                </el-dropdown-item>
                <el-dropdown-item style="text-align: center" @click.native="$router.push('/orderList')">查看全部</el-dropdown-item>
              </div>
              <span v-else class="no_oroder">暂时没有新的订单</span>
            </el-dropdown-menu>
          </el-dropdown>

          <span style="margin-right: 15px; cursor: pointer;">{{ nickName }}</span>

          <el-dropdown>
            <!-- 头像 -->
            <el-avatar class="mt-1 mr-3" style="cursor: pointer" shape="square" :src="userImage" fit="cover" />
            <el-dropdown-menu slot="dropdown" style="width: 100px">
              <el-dropdown-item @click.native="add">首页</el-dropdown-item>
              <el-dropdown-item @click.native="shopInfoEdit">
                店铺信息
              </el-dropdown-item>
              <router-link to="/editPassword"><el-dropdown-item>修改密码</el-dropdown-item></router-link>
              <el-dropdown-item @click.native="handleCommand">退出登录</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </el-header>
        <el-main class="main_box">
          <transition name="fade-transform" mode="out-in">
            <router-view :key="$route.path" />
          </transition>
        </el-main>
      </el-container>
    </el-container>
    <audio ref="order_remind_mp3" src="../assets/mp3/order_remind.mp3"></audio>
  </div>
</template>

<style lang="scss" scoped>
@import 'src/assets/css/mixin';
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 600px;
  min-height: 400px;
}
.app-wrapper {
  @include clearfix;
  position: relative;
  height: 100%;
  width: 100%;
}
.home-container {
  height: 100vh;
}
.el-aside {
  background-color: #304156;
  transition: width 0.3s;
  /*overflow: none;*/
}
.el_submenu {
  overflow-x: hidden;
}
.el-header {
  background-color: #fff;
  color: #333;
  line-height: 60px;
}
.el-menu {
  width: 100%;
  border: none;
}
.el-main {
  background-color: #f1f1f1;
}
.el-menu-item-group__title {
  padding: 0;
}

.toggle-button {
  float: left;
  font-size: 20px;
  line-height: 24px;
  color: #000000;
  text-align: center;
  letter-spacing: 0.2em;
  cursor: pointer;
}
.order_group{
  width: 370px;
  max-height: 518px;
  min-height: 156px;
  .order_item{
    margin-bottom: 20px;
    display: flex;
    .order_info{
      width: 183px;
      font-size: 14px;
      padding: 0px 15px;
      .order_header{
        font-size: 12px;
        line-height: 15px;
        display: flex;
        flex-wrap: wrap;
        overflow: hidden;
      }
      .order_title{
        word-break: normal;
        width: auto;
        display: block;
        white-space: pre-wrap;
        word-wrap: break-word;
        overflow: hidden;
        line-height: 20px;
        margin-top: 10px;
      }
    }
    .order_img{
      width: 64px;
      height: 36px;
    }
  }
  .no_oroder{
    color: #999;
    font-size: 14px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;
  }
}

.fade-transform-leave-active,
.fade-transform-enter-active {
  transition: all .5s;
}

.fade-transform-enter {
  opacity: 0;
  transform: translateX(-30px);
}

.fade-transform-leave-to {
  opacity: 0;
  transform: translateX(30px);
}

</style>

<script>
import noticeCmp from '@/components/notice_cmp/index.vue'
import chatWindowCmp from '@/components/chat_window_cmp/index.vue'
import { mapMutations, mapActions } from 'vuex'

export default {
  props: {
    id: {
      type: String,
      default: ''
    }
  },
  components: {
    noticeCmp,
    chatWindowCmp
  },
  data() {
    return {
      Review: [],
      queryInfo: {
        pageNo: 1,
        pageSize: 2
      },
      // 用户菜单的数据
      menulist: [],
      rongyunCmp: false, // 融云聊天窗口
      isCollapse: false,
      userId: '',
      nickName: localStorage.getItem('nickName') || 'woho',
      userImage: localStorage.getItem('userImage') || 'https://wpimg.wallstcn.com/f778738c-e4f8-4870-b634-56703b4acafe.gif',
    }
  },

  async created() {
    this.fetch()
    this.fetchForm()
    this.mimcLogin(localStorage.userId) // mimc登录
    await this.getConversationList()
    this.openWebSocket()
  },

  mounted() {
    if (document.body.clientWidth > 1200) {
      this.isCollapse = false
    } else {
      this.isCollapse = true
    }
    window.onresize = () => {
      if (document.body.clientWidth > 1200) {
        this.isCollapse = false
      } else {
        this.isCollapse = true
      }
    }
  },

  methods: {
    ...mapMutations(['mimcLogin']),
    ...mapActions(['getConversationList', 'clearCurrentUser']),

    // 选择菜单
    handleSelect (e) {
      // this.
    },
    // 点击按钮，切换菜单的折叠与展开
    toggleCollapse() {
      this.isCollapse = !this.isCollapse
    },
    // 退出功能
    handleCommand() {
      window.localStorage.clear()
      this.$notify({
        title: '成功',
        message: '退出登录成功',
        type: 'success'
      })
      this.$router.push('/')
    },
    // 店铺信息
    shopInfoEdit() {
      this.$router.push('/shopInfoEdit')
    },
    // 首页
    add() {
      this.$router.push('/home')
    },
    // 退出融云聊天框
    onChildFnMask() {
      this.rongyunCmp = !this.rongyunCmp
      if (!this.rongyunCmp) {
        this.clearCurrentUser() // 清除当前正在聊天人的信息
      }
    },
    // 打开融云聊天框
    onChildFnUser() {
      this.rongyunCmp = !this.rongyunCmp
    },
    // 菜单详清
    async fetch() {
      this.userId = localStorage.getItem('userId')
      const res = await this.$http.get(`/woho/sys/menu/treeMenu/${this.userId}`)
      this.menulist = res.data.data
    },
    //  按钮权限
    async fetchForm() {
      const res = await this.$http.get('/woho/sys/user/getPermission')
      if (res.data.success) {
        localStorage.btnPermission = res.data.data
      } else {
        this.$message.error(res.data.msg)
        this.$router.push('/')
      }
    },

    // 连接WebSocket
    openWebSocket () {
      if ('WebSocket' in window) {
        // 打开一个 web socket
        var ws = new WebSocket(`${this.$websocket}/websocket/${localStorage.shopId}`)
        ws.onmessage = (evt) => {
          console.log('数据已接收...', evt)
          const data = JSON.parse(evt.data)
          this._openOrderBox(data)
        }
      } else {
        // 浏览器不支持 WebSocket
        console.log('您的浏览器不支持 WebSocket!')
      }
    },

    // 订单弹窗提醒
    _openOrderBox (data) {
      this.$refs.order_remind_mp3.play()
      this.$confirm(`有新的订单, 订单号：${data.orderId}`, '提示', {
        confirmButtonText: '查看',
        cancelButtonText: '取消',
        type: 'info'
      }).then(() => {
        // eslint-disable-next-line
        if (data.type == 0) { // 平台
          this.$router.push({ path: `/orderList/orderContent`, query: { id: data.orderId }})
          // eslint-disable-next-line
        } else if (data.type == 2) { // 小超
          this.$router.push({ path: `/superMarket/orderDetail`, query: { id: data.orderId }})
        }
      }).catch(() => {
      })
    }
  }
}
</script>
